<template>
  <div class="app-container" id="personnelFiles">
    <div class="filter-container">
      <el-input v-model="listQuery.name" placeholder="姓名/身份证号码" class="filter-item" @keyup.enter.native="handleFilter" />
      <el-button class="filter-item filter-item-btn" type="primary" @click="handleFilter" icon="el-icon-search">搜索</el-button>
      <el-button class="filter-item filter-item-btn" type="primary" @click="handleCreate" icon="el-icon-circle-plus-outline">添加</el-button>
      <el-button class="filter-item filter-item-btn" type="primary" @click="exportExcel" icon="iconfont icondaochu" :loading="exportLoading">导出</el-button>
      <el-button class="filter-item filter-item-btn" type="primary" @click="uploadVisible=true" icon="iconfont icondaoru">导入</el-button>

    </div>
    <div class="tableBox">
      <el-table :key="tableKey" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName">
        <el-table-column label="序号" fixed prop="id" sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
        <el-table-column label="姓名" width="80px" align="center">
          <template slot-scope="{row}">
            <span class="link-type" @click="handleUpdate(row)">{{ row.name }}</span>
          </template>
        </el-table-column>

        <el-table-column label="绑定标签" width="200px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.rfid }}</span>
          </template>
        </el-table-column>
        <el-table-column label="状态" width="80px" align="center">
          <template slot-scope="scope">
            <span>{{ formatStatus(scope.row.status) }}</span>
          </template>
        </el-table-column>
        <el-table-column label="职位" width="110px" align="center">
          <template slot-scope="scope">
            <span>{{ formatPostStatus(scope.row.workTypes) }}</span>
          </template>
        </el-table-column>
        <el-table-column label="车组名称" min-width="110px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.groupName }}</span>
          </template>
        </el-table-column>
        <!-- <el-table-column label="所属大队" width="200px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.teamName }}</span>
          </template>
        </el-table-column> -->
        <el-table-column label="身份证号" width="100px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.userCode }}</span>
          </template>
        </el-table-column>
        <el-table-column label="性别" width="80px" align="center">
          <template slot-scope="scope">
            <span>{{ formatSexStatus(scope.row.sex)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="年龄" width="80px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.age>-1?scope.row.age:"" }}</span>
          </template>
        </el-table-column>
         <el-table-column label="电话号码" width="120px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.telNum }}</span>
          </template>
        </el-table-column>
         <el-table-column label="地址" min-width="120px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.address }}</span>
          </template>
        </el-table-column>
        <el-table-column label="入职时间" width="200px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.officeTimeShow }}</span>
          </template>
        </el-table-column>

        <el-table-column label="头像" min-width="80px" align="center" fixed="right">
          <template slot-scope="{row}">
            <span v-if="row.photoId" @click="popShow(row)" style="cursor:pointer">点击显示</span>
            <span v-else style="color: red;">暂无照片</span>

          </template>

        </el-table-column>
        <el-table-column label="操作" align="center" min-width="400" fixed="right" class-name="small-padding fixed-width">
          <template slot-scope="{row}">
            <el-button type="primary" size="mini" @click="handleBind(row)" icon="iconfont iconsuo" v-if="row.rfidId==null">绑定标签</el-button>
            <el-button type="warning" size="mini" @click="handleUnbind(row)" v-else icon="iconfont iconjiesuo5">解绑标签</el-button>
            <el-button type="primary" size="mini" @click="handleUpdate(row)" icon="el-icon-edit-outline">编辑</el-button>
            <el-button type="danger" size="mini" @click="deleteConfirmOpen(row)" icon="el-icon-delete">删除</el-button>
            <el-button type="primary" size="mini" @click="handleUpload(row)" icon="iconfont iconshangchuan">上传</el-button>

          </template>
        </el-table-column>
      </el-table>
    </div>

    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />

    <el-dialog id="myDialog" @close="scrollTop" :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible" center>
      <el-form ref="dataForm" :rules="rules" :model="temp" label-position="left" class="formStyle">
        <el-form-item label="姓名" prop="name" v-if="dialogStatus=='create'">
          <el-input v-model="temp.name" />
        </el-form-item>
        <el-form-item label="身份证号" prop="userCode">
          <el-input v-model="temp.userCode" />
        </el-form-item>
        <el-form-item label="性别" prop="sex">
          <el-select v-model.number="temp.sex" class="filter-item" placeholder="Please select">
            <el-option v-for="item in sexOptions" :key="item.key" :label="item.display_name" :value="item.key" />
          </el-select>
        </el-form-item>
        <el-form-item label="年龄" prop="age">
          <el-input v-model.number="temp.age" />
        </el-form-item>
        <el-form-item label="电话号码" prop="telNum">
          <el-input v-model="temp.telNum" />
        </el-form-item>
        <el-form-item label="地址" prop="address">
          <el-input v-model="temp.address" />
        </el-form-item>
        <el-form-item label="入职时间" prop="officeTimeShow">
          <el-date-picker value-format="yyyy-MM-dd HH:mm:ss" v-model="temp.officeTimeShow" align="right" type="datetime" placeholder="选择日期" :picker-options="pickerOptions"></el-date-picker>
        </el-form-item>
        <el-form-item label="所属大队" prop="teamId" v-if="dialogStatus=='update'&&power==1">
          <el-select v-model.number="temp.teamId" class="filter-item" placeholder="Please select">
            <el-option
              v-for="item in largeTeamOptions"
              :key="item.id"
              :label="item.teamName"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="状态" prop="status">
          <el-select v-model.number="temp.status" class="filter-item" placeholder="Please select">
            <el-option v-for="item in personStatusOptions" :key="item.key" :label="item.display_name" :value="item.key" />
          </el-select>
        </el-form-item>

        <el-form-item label="职位" prop="workTypes">
          <el-select multiple v-model="temp.workTypes" class="filter-item" placeholder="Please select">
            <el-option v-for="item in postOptions" :key="item.key" :label="item.display_name" :value="item.key" />
          </el-select>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogStatus==='create'?createData():updateData()">确定</el-button>
      </div>
    </el-dialog>
    <el-dialog id="myDialog" @close="scrollTop" :title="bindMap[bindDialogStatus]" :visible.sync="BindDialogFormVisible" center>
      <el-form ref="bindForm" :rules="bindRules" label-position="left" class="bindForm formStyle" :model="bindTemp">
        <el-form-item :label="`${bindMap[bindDialogStatus]}类型`" prop="type">
          <el-select filterable v-model="bindTemp.type" class="filter-item" placeholder="Please select">
            <el-option v-for="item in bindTypeOptions" :key="item.id" :label="item.name" :value="item.id" :disabled="item.disabled" />
          </el-select>
        </el-form-item>

        <el-form-item label="所属大队" prop="teamId" v-if="bindTemp.type==1&&bindDialogStatus=='bind'">
          <el-select filterable v-model="bindTemp.teamId " class="filter-item" placeholder="Please select">
            <el-option v-for="item in largeTeamOptions" :key="item.id" :label="item.teamName" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="标签" prop="rfidId" v-if="bindTemp.type==2&&bindDialogStatus=='bind'">
          <el-select filterable v-model="bindTemp.rfidId " class="filter-item" placeholder="Please select">
            <el-option v-for="item in rfidOptions" :key="item.id" :label="item.rfid" :value="item.id" />
          </el-select>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="bindDialogStatus=='bind'?bindSubmit():unbindSubmit()">确定</el-button>

      </div>
    </el-dialog>
    <el-dialog id="myDialog1" title="上传图片" :visible.sync="DialogUploadVisible" center>
      <el-upload class="upload-demo" ref="my-upload" :multiple="false" :limit="1" drag action="/gate/file/xxx/upload" accept=".jpg,.png" :before-upload="beforeUploadFile" :headers="{Authorization:'Bearer '+ token}" :on-success="uploadSuccess">
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
        <div class="el-upload__tip" slot="tip">只能上传jpg/png文件,且文件大小不得超过10M</div>
        <div class="el-upload__tip red" slot="tip">图片上传尺寸请尽量为 295*413 </div>

      </el-upload>
      <div slot="footer" class="dialog-footer">
        <!-- <el-button type="primary" >确定</el-button> -->

      </div>
    </el-dialog>
    <el-dialog id="myDialog2" width="30%" title="图片" :visible.sync="imgVisible" center>
      <img :src="taskSrc" class="taskSrc" alt="暂无图片">
    </el-dialog>
    <my-upload :visible.sync="uploadVisible" :uploadData="{type:1}" @dataUpdated="reqSuccess"></my-upload>

  </div>
</template>

<script>
import Pagination from "../../../components/Pagination"; // secondary package based on el-pagination
import myUpload from "@/components/myUpload"; // secondary package based on el-pagination

// 人员列表 添加人员 编辑人员 删除人员 查找人员
import {
  personList,
  personAdd,
  personUpdate,
  personDelete,
  personSearch,
  bindTeam,
  unbindTeam, bindRfid, unbindRfid, bindScanDevice,
  addWorkTypes, removeWorkType
} from "../../../api/managementArchives/personnelFiles";
import {
  imgDownload, savePhoto, bindPhoto, photoInfosQuery
} from "../../../api/schedule/schedule";
//大队，车组，枪支的下拉列表
import {
  teamsSelect,

  exportExcelReq
} from "../../../api/managementArchives/common";
import { queryR } from "../../../api/inOut/rfid";
const sexOptions = [
  { key: 1, display_name: "男" },
  { key: 0, display_name: "女" }
];

const personStatusOptions = [
  { key: 0, display_name: "在职" },
  { key: 1, display_name: "离职" },
  { key: 2, display_name: "请假" }
];
const postOptions = [
  { key: 1, display_name: "司机" },
  { key: 2, display_name: "车组长" },
  { key: 3, display_name: "押运员" },
  { key: 4, display_name: "业务员" },
  { key: 5, display_name: "大队长" },
  { key: 6, display_name: "枪管员" },
  { key: 7, display_name: "车管员" },
  { key: 8, display_name: "库管员" }
];
//未选择
const unSelect = (rule, value, callback) => {
  if (value == -1) {
    callback(new Error("请选择"));
  } else {
    callback();
  }
};
var checkTelNum = (rule, value, callback) => {
  if (!value) {
    callback();
  }
  setTimeout(() => {
    if (!(/^1[3456789]\d{9}$/.test(value))) {
      callback(new Error('手机号码有误，请重填'));
    } else {

      callback();

    }
  }, 1000);
};
let myLoading = undefined; // 弹窗服务调用
export default {
  name: "ComplexTable",
  components: { Pagination, myUpload },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      token: JSON.parse(sessionStorage.getItem("userInfo"))['Bearer '],
      tableKey: 0,
      list: null,
      total: 0,
      indexMethod: 1, // 序号
      listLoading: false,
      listQuery: {
        page: 1,
        limit: 10,
        name: ""
      },
      sexOptions, //性别下拉项
      largeTeamOptions: [{ id: -1, teamName: "请选择" }],
      personStatusOptions, //状态下拉项
      postOptions, //职位下拉项
      groupOptions: [{ id: null, groupName: "请选择" }], //最终车组下拉（根据情况是groupOptionsAll或者是groupOptionsPart）
      groupOptionsAll: [{ id: null, groupName: "请选择" }],
      groupOptionsPart: [{ id: null, groupName: "请选择" }],
      gunOptions: [{ id: null, iMei: "请选择" }], //枪号下拉
      temp: {
        name: null,
        userCode: "",
        sex: 1,
        age: "",
        officeTime: null,
        // updateTime: null,
        status: 0,
        // groupId: null,
        // groupName: null,
        workTypes: [],
        // teamId: null,
        // teamName: null,
        // gunId: null,
        // guniMei: null
      }, //编辑或者添加需要提交的 数据
      dialogFormVisible: false, //弹窗显隐
      dialogStatus: "", //控制弹窗是添加还是编辑
      textMap: {
        update: "编辑",
        create: "添加"
      },
      //表单验证
      rules: {
        name: [{ required: true, message: "请填写姓名", trigger: "blur" }],
        userCode: [
          { required: true, message: "请填写身份证号", trigger: "blur" }
        ],
        sex: [
          {
            required: true,
            type: "number",
            message: "请选择性别",
            trigger: "change"
          }
        ],
        workTypes: [
          {
            required: true,
            message: "请选择职位",
            trigger: "change"
          }
        ],
        telNum: [
          { validator: checkTelNum, trigger: 'blur' }
        ]

      },
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "昨天",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "一周前",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      bindTypeOptions: [
        { id: -1, name: "请选择" },
        // { id: 1, name: "大队" },
        { id: 2, name: "标签" }

      ], //绑定类型下拉数据
      bindTemp: {


      },

      BindDialogFormVisible: false,
      bindDialogStatus: "",//控制是绑定还是解绑窗口
      bindMap: {
        bind: "绑定",
        unbind: "解绑"
      },
      bindRules: {
        type: [
          { required: true, message: "请选择绑定类型", trigger: "change", validator: unSelect }
        ],
        teamId: [
          { required: true, message: "请选择绑定大队", trigger: "change", validator: unSelect }
        ],
        rfidId: [
          { required: true, message: "请选择绑定标签", trigger: "change" }
        ],
        scanDeviceId: [
          { required: true, message: "请选择扫描设备", trigger: "change", validator: unSelect }
        ]
      },
      rfidOptions: [],
      scanOptions: [],
      fileList: [],
      DialogUploadVisible: false,
      personId: null,
      imgVisible: false,
      taskSrc: '',
      initialWorkTypes: [],
      uploadVisible: false,
      exportLoading: false,
    };
  },
  created() {
    this.getList(); //初始化获取人员列表
    this.getSelect(); //初始化弹窗的下拉框

  },
  methods: {
     deleteConfirmOpen(row) {
      this.$confirm('确定进行此操作吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.handleDelete(row)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消'
        });
      });
    },
    // 上传文件之前的钩子, 参数为上传的文件,若返回 false 或者返回 Promise 且被 reject，则停止上传
    beforeUploadFile(file) {

      let extension = file.name.substring(file.name.lastIndexOf('.') + 1);
      let size = file.size / 1024 / 1024;
      if (extension !== 'jpg' && extension !== 'png' && extension !== 'JPG' && extension !== 'PNG') {
        this.$message.warning('只能上传后缀是.jpg或者.png的文件');
        return false;
      }
      if (size > 10) {
        this.$message.warning('文件大小不得超过10M');
        return false;
      }
    },
    reqSuccess: function (val) {  //val用于接收子组件传过来的值
      if (val == true) {
        this.getList();
      }
    },
    popShow(row) {
      this.listLoading = true;
      this.taskSrc = "";
      let that = this;
      if (row.photoId == null) {
        this.imgVisible = true;
        this.listLoading = false;
        return;
      }
      photoInfosQuery({ id: row.photoId, type: [1] })
        .then(res => {
          if (res.status == 0) {
            if (res.data && res.data.length == 0) {
              that.imgVisible = true;
              return;
            }
            imgDownload({ path: res.data[0].path, fileName: res.data[0].fileName })
              .then(res1 => {
                that.imgVisible = true;
                that.listLoading = false;
                that.taskSrc = window.URL.createObjectURL(res1)
              })
              .finally(() => {
                that.listLoading = false;
              });

          } else {
            this.$message.error(res.msg);
          }
        }).catch(() => {
          that.listLoading = false;
        })
        .finally(() => {
        });

    },
    handleUpload(row) {
      this.DialogUploadVisible = true;
      this.$nextTick(() => {
        this.$refs['my-upload'].clearFiles();
      })

      this.personId = row.id
    },
    uploadSuccess(res) {
      if (res.data == null) {
        return;
      }
      let p1 = new Promise((resolve, reject) => {
        savePhoto({
          fileName: res.data.filename,
          path: res.data.path,
          type: 1,
          status: 1
        })
          .then(res => {
            resolve(res);

          }).catch(err => {
            reject(err);

          })


      })

      p1.then((res) => {

        bindPhoto({
          personId: this.personId,
          photoId: res.data
        })
          .then(res1 => {
            if (res1.status == 0) {
              this.DialogUploadVisible = false;
              this.$message.success(res1.msg);
              this.getList();

            } else {
              this.$message.error(res1.msg);
            }
          }).catch(() => {
            this.$message.error(res1.msg);
          })
          .finally(() => {
            _t.listLoading = false;
          });


      }, (err) => {
        _t.listLoading = false;
        this.$message.error(err.msg);
      })



    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`);
    },
    handleBind(row) {
      let rfidType = null;
      let workTypes = [];

      for (let i = 0; i < row.workTypes.length; i++) {
        let workType = row.workTypes[i];

        switch (workType) {
          case 1:
            rfidType = 10;
            break;
          case 2:
            rfidType = 10;
            break;
          case 3:
            rfidType = 10;
            break;
          case 4:
            rfidType = 10;
            break;
          case 5:
            rfidType = 10;
            break;
          case 6:
            rfidType = 6;
            break;
          case 7:
            rfidType = 7;
            break;
          case 8:
            rfidType = 8;
            break;
          case 9:
            rfidType = 9;
            break;
          case 10:
            rfidType = 10;
            break;
          case 80:
            rfidType = 80;
            break;
          case 90:
            rfidType = 90;
            break;
          case 99:
            rfidType = 99;
            break;
        }
        if (rfidType !== null && workTypes.indexOf(rfidType) == -1) {
          workTypes.push(rfidType)
        }
      }

      this.getRfid(workTypes, row);

      this.bindDialogStatus = "bind";
      this.bindTemp = {
        personId: row.id,
        teamId: row.teamId,
        rfidId: null,

        scanDeviceId: row.scanDeviceId,
      }
      this.BindDialogFormVisible = true;
    },
    handleUnbind(row) {
      // if (row.teamId == -1) { this.bindTypeOptions[1].disabled = true }
      if (row.rfidId == null) { this.bindTypeOptions[1].disabled = true }
      // this.bindDialogStatus = "unbind";
      this.bindTemp = {
        personId: row.id
      }
      this.bindDialogStatus = "unbind";

      this.BindDialogFormVisible = true;
    },
    unbindSubmit() {
      this.$refs["bindForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });

          let subData, request;
          subData = {
            personId: this.bindTemp.personId
          }
          if (this.bindTemp.type == 1) {

            request = unbindTeam;

          } else if (this.bindTemp.type == 2) {

            request = unbindRfid;


          }
          request(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.getSelect();

                this.$message.success(res.msg);
                this.BindDialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        }
      })
    },
    bindSubmit() {
      this.$refs["bindForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          let subData, request;
          if (this.bindTemp.type == 1) {
            subData = {
              personId: this.bindTemp.personId,
              teamId: this.bindTemp.teamId,
            }
            request = bindTeam;

          } else if (this.bindTemp.type == 2) {
            subData = {
              personId: this.bindTemp.personId,
              rfidId: this.bindTemp.rfidId,
            }
            request = bindRfid;
          } else if (this.bindTemp.type == 3) {
            subData = {
              carId: this.bindTemp.carId,
              scanDeviceId: this.bindTemp.scanDeviceId,
            }
            request = bindScanDevice;
          }



          request(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success(res.msg);
                this.BindDialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });

        }
      })
    },

    //获取人员列表
    getList() {
      let row = {}
      let _t = this;
      _t.listLoading = true;
      personList({
        page: this.listQuery.page,
        limit: this.listQuery.limit,
        status: [0, 2]
      })
        .then(res => {
          if (res.status == 0) {
            this.list = res.data;
            this.total = res.count;
            this.indexMethod = (_t.listQuery.page - 1) * _t.listQuery.limit + 1;

          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          _t.listLoading = false;
        });
    },
    //搜索
    handleFilter() {
      if (this.listQuery.name == "" || this.listQuery.name == undefined) {
        this.listQuery.name = null;
        this.getList();
        return;
      }
      personSearch({
        keyword: this.listQuery.name
      }).then(res => {
        if (res.status == 0) {
          this.listQuery.page = 1;
          this.listLoading = false;
          this.list = res.data;
          this.total = 0; //不显示分页器
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    //重置
    resetTemp() {
      this.temp = {
        name: null,
        userCode: "",
        sex: 1,
        age: "",
        officeTime: null,
        status: 0,
        workTypes: []
      };
    },
    //点击添加
    handleCreate() {
      this.resetTemp();
      this.dialogStatus = "create";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    //添加人员
    createData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          this.temp.officeTime = new Date(this.temp.officeTimeShow).getTime(); //入职时间
          let subData = { ...this.temp };
          if (!subData.age && subData.age !== 0) {
            subData.age = -1
          }
          if (!this.officeTime) {
            delete subData.officeTime
          }
          delete subData.officeTimeShow
          if (!subData.telNum) {
            subData.telNum = ''
          }
          if (!subData.address) {
            subData.address = ''
          }
          personAdd(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success("添加成功");
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    //点击编辑
    handleUpdate(row) {
      this.temp = {
        id: row.id,
        userCode: row.userCode,
        sex: row.sex,
        status: row.status,
        workTypes: row.workTypes,
        officeTime: row.officeTime,
        officeTimeShow: row.officeTimeShow,
        address: row.address,
        telNum: row.telNum
      }


      this.initialWorkTypes = row.workTypes;
      if (row.age !== -1) {
        this.temp.age = row.age
      }
      this.dialogStatus = "update";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    //编辑提交
    updateData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });

          let subData = { ...this.temp };
          if (!subData.age && subData.age !== 0) {
            subData.age = -1;
          }
          if (this.temp.officeTimeShow) {
            subData.officeTime = new Date(this.temp.officeTimeShow).getTime();
          } else {
            delete subData.officeTime
          }
          if (!subData.telNum) {
            subData.telNum = ''
          }
          if (!subData.address) {
            subData.address = ''
          }



          let workTypes = subData.workTypes;
          delete subData.officeTimeShow;
          delete subData.workTypes;
          let addWorkTypes1 = [], removeWorkTypes = [], removeReq = [];
          for (let i = 0; i < this.initialWorkTypes.length; i++) {
            if (workTypes.indexOf(this.initialWorkTypes[i]) == -1) {
              removeReq.push(new Promise((resolve, reject) => {
                removeWorkType({ infoId: subData.id, workType: this.initialWorkTypes[i] })
                  .then(res => {

                    resolve();
                  }).catch(() => {
                    reject()
                  })
              }))
            }
          }
          for (let i = 0; i < workTypes.length; i++) {
            if (this.initialWorkTypes.indexOf(workTypes[i]) == -1) {
              addWorkTypes1.push(workTypes[i])
            }
          }
          let p1 = new Promise((resolve, reject) => {
            personUpdate(subData)
              .then(res => {

                resolve();
              }).catch(() => {
                reject()
              })

          })
          let p2 = new Promise((resolve, reject) => {
            addWorkTypes({ infoId: subData.id, workTypes: addWorkTypes1 })
              .then(res => {

                resolve();
              }).catch((err) => {
                console.warn(err)
                reject()
              })

          })

          Promise.all(removeReq.concat([p1, p2])).then((values) => {
            this.getList();
            this.$message.success("编辑成功");
          }).catch((err) => {
            this.$message.error('出错啦')
          }).finally((res) => {
            this.dialogFormVisible = false;
            myLoading.close();

          })

        } else {
          return false;
        }
      });
    },
    //删除
    handleDelete(row) {
      let _t = this;
      _t.listLoading = true;
      personDelete({
        personId: row.id
      })
        .then(res => {
          if (res.status == 0) {
            if (_t.listQuery.page > 1 && _t.list.length <= 1) {
              // 最后一条返回上一页
              _t.listQuery.page -= 1;
            }
            this.getList();
            _t.$message.success("刪除成功");
          } else {
            this.$message.error(res.msg);
            _t.listLoading = false;
          }
        })
        .catch(err => {
          _t.listLoading = false;
        });
    },
    //性别显示
    formatSexStatus(status) {
      if (status == 0) {
        return "女";
      } else if (status == 1) {
        return "男";
      }
    },
    //状态显示
    formatStatus(status) {
      if (status == 0) {
        return "在职";
      } else if (status == 1) {
        return "离职";
      } else {
        return "请假";
      }
    },
    //职位显示
    formatPostStatus(workTypes) {
      let arr = [];
      let status;
      if (!workTypes) {
        return;
      }
      for (let i = 0; i < workTypes.length; i++) {
        status = workTypes[i];
        if (status == 1) {
          arr.push("司机");
        } else if (status == 2) {
          arr.push("车组长");

        } else if (status == 3) {
          arr.push("押运员");

        } else if (status == 4) {
          arr.push("业务员");

        } else if (status == 5) {
          arr.push("大队长");

        } else if (status == 6) {
          arr.push("枪管员");

        } else if (status == 7) {
          arr.push("车管员");

        }
        else if (status == 8) {
          arr.push("库管员");

        }

      }
      return arr.toString();

    },
    //筛选出select选择的值和内容，参数分别代表（select的v-model值，option数组，option值，option内容，后台需要的id,后台需要的name）
    selectFilter(vId, selectList, id, name, id1, name1) {
      let obj = {};
      obj = selectList.find(item => {
        //这里的selectList就是上面遍历的数据源
        return item[id] === vId; //筛选出匹配数据
      });
      this.temp[name1] = obj[name];
    },
    getRfid(type, row) {
      let that = this;
      this.rfidOptions = [];
      queryR({ rfidType: type, status: [0] }).then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            return;
          }
          that.rfidOptions = res.data;
          that.rfidOptions.unshift({ id: null, rfid: "请选择" });
        } else {
          if (res.data == null) {
            this.rfidOptions = [];
            this.rfidOptions.pop();
          }
          this.$message.error(res.msg);
        }
      });
    },
    //初始化下拉框
    getSelect() {
      var that = this;
      //大队
      teamsSelect({ status: 1 }).then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            return;
          }
          that.largeTeamOptions = res.data;
          that.largeTeamOptions.unshift({ id: -1, teamName: "请选择" });
        } else {
          if (res.data == null) {
            this.largeTeamOptions = [];
            this.largeTeamOptions.pop();
          }
          this.$message.error(res.msg);
        }
      });



    },

    scrollTop() {
      // 弹窗滚动到顶部
      let myDialog = document.querySelector("#myDialog .el-dialog__body");
      myDialog.scrollTop = 0;
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
    exportExcel() {
      let _t = this;
      this.exportLoading = true;
      // 查找数据
      exportExcelReq({ excelType: 4, title: '人员信息' }).then(res => {
        try {
          let enc = new TextDecoder('utf-8')
          let data = JSON.parse(enc.decode(new Uint8Array(res)))
          this.$message({
            message: data.msg,
            type: 'warning'
          });

        } catch (error) {
          let disposition = res.headers;
          let fileName = "人员信息" + ".xls";
          let blob = new Blob([res], { type: 'application/excel' }); // 假设文件为pdf
          let link = document.createElement("a");
          link.href = window.URL.createObjectURL(blob);
          link.download = fileName;
          link.click();
          link.remove();
        }
      }).finally(() => {
        this.exportLoading = false;
      });
    },

  }
};
</script>
